/**
 * token
 */
function setTimeGetToken(appid,appkey){
	// UUID
	let len = uuid.create().toString().length;
	let s   = uuid.create().toString();
	let str = s.substring(0,8) + s.substring(9,13) + s.substring(14,18) + s.substring(19,23) + s.substring(24,len);
	// 
	let timestamp = (new Date()).valueOf();
	let num = new Number(timestamp);
	// 
	let sign = 'appid=' + appid + "&nonce=" + str + "&timestamp=" + num.toString();
	
	account.authorize({
		type: 'code',
		success: function(data) {
			let params = {
				thirdUId: data.code,
				type:1
			}
			let strObj = {
				"appId"       : appid,
				"timestamp"   : num.toString(),
				"nonce"       : str,
				"Content-Type": 'application/json'
			}
			strObj.signature  = SHA.sha256(sign + JSON.stringify(params) + appkey);
			fly.post(host + '/v1/iotopen/user/token/get', params, {headers:strObj}).then(function (response) {
				let data_accesstoken = response;
				if (data_accesstoken.status && data_accesstoken.status === 200) {
					let result_data = typeof data_accesstoken.data === "object"? data_accesstoken.data:JSON.parse(data_accesstoken.data);
					if (!parseInt(result_data.code)) {
						let timestamp = Date.parse(new Date());
						let obj = {
							openid: result_data.openId,
							accessToken: result_data.accessToken,
							time: parseInt(timestamp) + 3600000
						}
						storage.set({
							key: 'openidToken',
							value: JSON.stringify(obj),
							success: function (data) {
								
							},
							fail: function (data, code) {
								
							}
						})
					}
				}
				setGet(appid,appkey);
			}).catch(function (error) {
			});
		},
		fail: function(data, code) {
				console.log("data"+data+":::code"+code)
		}
	})
}
/**
 * 
 */
function setGet(appid,appkey){
	getStorage().then((res) => {
		let t = parseInt(res);
		setTimeout(function(){
			setTimeGetToken(appid,appkey);
		},t)
	}).catch((error) => {
		setTimeGetToken(appid,appkey)
	})
}
/**
 * 
 */
function getStorage(){
	let time = Date.parse(new Date());
	var p = new Promise(function(resolve, reject){
		storage.get({
			key:"openidToken",
			success:function (data){
				if (data == '') {
					reject()
				} else{
					let obj = JSON.parse(data);
					if (parseInt(time) > parseInt(obj.time)) {
						let set = parseInt(time) - parseInt(obj.time)
						resolve(set)
					} else{
						reject()
					}
				}
			}
		})
	})
	return p;
}
